.field-title {
    margin-top: 20px;
    margin-bottom: 10px;
}

.field-values {
    display: flex;
    justify-content: left;

    &-images {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: 1fr;
        grid-column-gap: 20px;
        grid-row-gap: 20px;
        text-align: center;

        @media screen and (max-width: 500px) {
            grid-template-columns: repeat(2, 1fr);
            grid-template-rows: repeat(3, 1fr);
            grid-column-gap: 0px;
            grid-row-gap: 0px;
            grid-column-gap: 5px;
            grid-row-gap: 5px;
        }

        .image-field {
            display: flex;
            height: 200px;
            width: 100%;
            background-size: cover;
            background-position: center center;

            &:nth-child(1){
                background-image: url(hz.jpg);
            }
            &:nth-child(2){
                background-image: url(minimal.jpg);
            }
            &:nth-child(3){
                background-image: url(loft.jpg);
            }
            &:nth-child(4){
                background-image: url(contemporary.jpg);
            }
            &:nth-child(5){
                background-image: url(scandinavian.jpg);
            }
            &:nth-child(6){
                background-image: url(art-decor.jpg);
            }

            @media screen and (max-width: 500px) {
                &:nth-child(1){
                    grid-area: 1 / 1 / 2 / 2;
                }
                &:nth-child(2){
                    grid-area: 1 / 2 / 2 / 3;
                }
                &:nth-child(3){
                    grid-area: 2 / 1 / 3 / 2;
                }
                &:nth-child(4){
                    grid-area: 2 / 2 / 3 / 3;
                }
                &:nth-child(5){
                    grid-area: 3 / 1 / 4 / 2;
                }
                &:nth-child(6){
                    grid-area: 3 / 2 / 4 / 3;
                }
            }

            .btn-image {
                width: 100%;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;

                .btn-inp {
                    overflow: hidden;
                    background-color: #f3f3f38f;
                    padding: 4px 10px;
                    width: 100%;
                }
                
                &:hover {
                    cursor: pointer;
                }
            }

            input {
                display: none;
                &:checked + label {
                    cursor: default;
                    color: #fff;
                    transition: color 200ms;
                    background: #ed6200a8;
                }
            }
        }
    }
}

.field-values {
    .btn-calc {
        overflow: hidden;
        position: relative;
        border: 1px solid #a7a7a7;
        padding: 10px;
        text-align: center;
        transition: background 600ms ease, color 600ms ease;
    }

    @media screen and (min-width: 501px) {
        .btn-calc {
            display: inline-block;
        
            &:not(:last-of-type) {
                border-right: 0;
            }
            &:last-of-type {
                border-radius: 0 8px 8px 0;
            }
            
            &:first-of-type {
                border-radius: 8px 0 0 8px;
            }
        }
    }

    @media screen and (max-width: 500px) {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-column-gap: 0px;
        grid-row-gap: 0px;

        .btn-calc:nth-of-type(1) { grid-area: 1 / 1 / 2 / 2; }
        .btn-calc:nth-of-type(2) {
            grid-area: 1 / 2 / 2 / 3;
            &[for="room-type__house"] {
                border-right: 1px solid #a7a7a7;
            }
        }
        .btn-calc:nth-of-type(3) { grid-area: 2 / 1 / 3 / 2; }
        .btn-calc:nth-of-type(4) { grid-area: 2 / 2 / 3 / 3; }

        .btn-calc {
            border-radius: 0;
            font-size: 0.8rem;
            padding: 10px 0;
        
            &:not(:last-of-type) {
                border-right: 0;
            }
        }


    }
}

input[type="radio"].toggle-calc {
    display: none;
    & + label{
        cursor: pointer;
        z-index: 0;
        &:hover{
            background: none;
            color: #333;
        }
        &:after{
            background: #0f4c81;
            content: "";
            height: 100%;
            position: absolute;
            top: 0;
            transition: left 200ms cubic-bezier(0.77, 0, 0.175, 1);
            width: 100%;
            z-index: -1;
        }
    }
    
    &:checked + label {
        cursor: default;
        color: #fff;
        transition: color 200ms;
    }
}

